import React,{Component} from "react";
import CSSModules from "react-css-modules";
import styles from "./goodsList.css";
import GoodsItem from "../goodsItem/goodsItem";

class GoodsList extends Component{
    render(){
        const simulateData=[];
        for(let i=1;i<=50;i++){
            simulateData.push({id:i,name:"免疫球蛋白益生菌"+i,specification:"300ml/盒"})
        }
        const goodsList=[{cate:"保健药品",data:simulateData}];
        return(
            <div styleName="root">
                <ul styleName="sidebar">
                    <li>生物药品</li>
                    <li className='active'>保健药品</li>
                    <li>中成药</li>
                    <li>中药饮片</li>
                </ul>
                <div styleName="goodsList">
                    {
                        goodsList.map((goods,index)=>{
                            return (
                                <div key={index}>
                                    <h4 styleName="title">{goods.cate}</h4>
                                    <ul>
                                    {
                                        goods.data.map(item=>{
                                            return <GoodsItem key={item.id} name={item.name} specification={item.specification}/>;
                                        })
                                    }
                                    </ul>
                                </div>
                            )
                        })
                    }
                </div>
            </div>
        )
    }
}
export default CSSModules(GoodsList,styles);


